import {changeValue} from '@github-ui/form-utils'
import {fromEvent} from '@github-ui/subscription'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {parseHTML} from '@github-ui/parse-html'
import {TemplateInstance} from '@github/template-parts'

observe('.js-prebuilds-view-runs-menu-item', {
  subscribe: el =>
    fromEvent(el, 'socket:message', function (event: Event) {
      const disabledRunItem = event.currentTarget as HTMLElement
      const enabledRunItem = disabledRunItem.parentElement?.querySelector('li.config-view-runs-item') as HTMLElement
      if (!disabledRunItem || !enabledRunItem) return
      disabledRunItem.hidden = true
      enabledRunItem.hidden = false
    }),
})

observe('.js-codespaces-prebuilds-vscs-targets', container => {
  const defaultInput = container.querySelector('.js-codespaces-prebuilds-vscs-target:checked') as HTMLInputElement
  handleVSCSTargetLocations(defaultInput)
})

on('click', '.js-codespaces-prebuilds-trigger', e => {
  const input = e.currentTarget as HTMLInputElement
  const schedule = document.getElementById('codespaces_prebuild_configuration_schedule') as HTMLSpanElement
  if (!input || !schedule) return

  if (input.value === 'schedule') {
    schedule.hidden = false
  } else {
    schedule.hidden = true
  }
})

on('click', '.js-codespaces-prebuilds-vscs-target', e => {
  const input = e.currentTarget as HTMLInputElement
  handleVSCSTargetLocations(input)
})

on('change', '.js-codespaces-prebuilds-specific-regions', () => {
  toggleSubmitButtonOnLocationChanges(false)
})

on('change', '.js-codespaces-prebuilds-locations', () => {
  toggleSubmitButtonOnLocationChanges(false)
})

on('click', '.js-codespaces-toggle-advanced-options', () => {
  const advancedOptions = document.getElementById('advanced-options') as HTMLInputElement
  const advancedOptionsButton = document.getElementById('toggle-advanced-options') as HTMLButtonElement

  const showMessage = document.querySelector('.js-show-options')?.getAttribute('prebuild-show-advanced-options') ?? null
  const hideMessage = document.querySelector('.js-hide-options')?.getAttribute('prebuild-hide-advanced-options') ?? null

  if (!advancedOptions || !advancedOptionsButton || !showMessage || !hideMessage) return

  if (advancedOptions.style.display === 'none') {
    advancedOptions.style.display = 'block'
    advancedOptionsButton.textContent = hideMessage
  } else {
    advancedOptions.style.display = 'none'
    advancedOptionsButton.textContent = showMessage
  }
})

async function handleVSCSTargetLocations(vscsTargetInput: HTMLInputElement) {
  if (!vscsTargetInput) return

  const geosForVscs = vscsTargetInput.getAttribute('data-codespace-geos')!.split(' ')
  if (!geosForVscs) return

  const specificRegionsInput = document.querySelector<HTMLInputElement>('.js-codespaces-prebuilds-specific-regions')
  const locationInputs = document.querySelectorAll<HTMLInputElement>('.js-codespaces-prebuilds-locations')
  const vscsDeveloperTargetLocationInput = document.getElementById(
    'vscs_developer_target_location_input',
  ) as HTMLInputElement

  if (!specificRegionsInput || locationInputs.length === 0 || !vscsDeveloperTargetLocationInput) return

  resetForm(specificRegionsInput, locationInputs)
  toggleLocations(specificRegionsInput, geosForVscs, locationInputs)
}

function resetForm(specificRegionsInput: HTMLInputElement, locationInputs: NodeListOf<HTMLInputElement>) {
  specificRegionsInput.disabled = false
  for (const input of locationInputs) {
    input.disabled = false
    toggleUnavailableText(input, true)
  }
}

function toggleLocations(
  specificRegionsInput: HTMLInputElement,
  availableLocations: string[],
  locationInputs: NodeListOf<HTMLInputElement>,
) {
  for (const input of locationInputs) {
    if (isGeoAvailable(input.value, availableLocations)) {
      input.disabled = false
      input.checked = input.getAttribute('data-originally-checked') === 'true'
      dispatchInputOnChangeEvent(input)
      toggleUnavailableText(input, true)
    } else {
      input.checked = false
      input.disabled = true
      specificRegionsInput.checked = true
      specificRegionsInput.disabled = true
      toggleUnavailableText(input, false)
    }
  }
}

function isGeoAvailable(geo: string, availableLocations: string[]) {
  return availableLocations.includes(geo)
}

function toggleUnavailableText(input: HTMLInputElement, hide: boolean) {
  const helpText = input.parentElement?.querySelector(
    '.js-codespaces-prebuilds-vscs-location-unavailable',
  ) as HTMLSpanElement

  if (helpText) helpText.hidden = hide
}

function toggleSubmitButtonOnLocationChanges(forceEnable: boolean) {
  const submitButton = document.querySelector<HTMLButtonElement>('.js-submit-codespace-prebuild-configuration-button')
  const specificRegionsInput = document.querySelector<HTMLInputElement>('.js-codespaces-prebuilds-specific-regions')

  if (!submitButton || !specificRegionsInput) return

  if (forceEnable) {
    submitButton.disabled = false
  } else {
    const locationInputs = document.querySelectorAll<HTMLInputElement>('.js-codespaces-prebuilds-locations')
    const atleastOnelocationSelected = Array.from(locationInputs).some(input => input.checked)

    if (atleastOnelocationSelected || !specificRegionsInput.checked) {
      submitButton.disabled = false
    } else {
      submitButton.disabled = true
    }
  }
}

on('click', '.member-suggestion', async function (event) {
  if (!(event.target instanceof Element)) return

  // get new actor to notify
  const searchInput = document.querySelector<HTMLInputElement>('input.auto-search-input')
  const actorIdentifier = searchInput?.value

  // find out if actor is user or team
  const type = event.target.closest<HTMLLIElement>('li')?.getAttribute('data-user-or-team')

  // get url for add actor endpoint
  const url = searchInput?.getAttribute('data-controller-path-prebuild-update-actors')

  const token = document.getElementById('update-notifiers-csrf')?.getAttribute('value')

  if (!actorIdentifier || !token || !url || !type) {
    displayErrorMessageOnFailure()
    return
  }

  // get list of actors to notify
  const userListUsernames = getUserList()
  const teamListSlugs = getTeamList()

  if (type === 'user') {
    userListUsernames.push(actorIdentifier)
  } else if (type === 'team') {
    teamListSlugs.push(actorIdentifier)
  }

  const requestBody = createNotifyActorsRequestBody(userListUsernames, teamListSlugs)
  const elementToReRender = document.getElementById('notify-options')

  if (elementToReRender == null) {
    displayErrorMessageOnFailure()
    return
  }

  sendPostRequestAndRenderPartialView(url, requestBody, token, elementToReRender)
})

on('click', '.js-remove-user-button', async function (event) {
  if (!(event.target instanceof Element)) return

  // get url for add actor endpoint
  const searchInput = document.querySelector<HTMLInputElement>('input.auto-search-input')
  const url = searchInput?.getAttribute('data-controller-path-prebuild-update-actors')

  // get csrf token
  const token = document.getElementById('update-notifiers-csrf')?.getAttribute('value')

  // find out if actor to remove is a user or team
  const type = event.target.closest<HTMLLIElement>('li')?.getAttribute('prebuild-data-user-or-team')
  let actorIdentifier: string | null | undefined = null

  if (type === 'user') {
    actorIdentifier = event.target.closest<HTMLLIElement>('li')?.getAttribute('prebuild-data-bulk-actions-id-user')
  } else if (type === 'team') {
    actorIdentifier = event.target.closest<HTMLLIElement>('li')?.getAttribute('prebuild-data-bulk-actions-id-team')
  }

  if (!actorIdentifier || !token || !url || !type) {
    displayErrorMessageOnFailure()
    return
  }

  // get list of actors to notify
  let userListUsernames = getUserList()
  let teamListSlugs = getTeamList()

  if (type === 'user') {
    userListUsernames = userListUsernames.filter(user => user !== actorIdentifier)
  } else if (type === 'team') {
    teamListSlugs = teamListSlugs.filter(team => team !== actorIdentifier)
  }

  const requestBody = createNotifyActorsRequestBody(userListUsernames, teamListSlugs)

  const elementToReRender = document.getElementById('notify-options')

  if (elementToReRender == null) {
    displayErrorMessageOnFailure()
    return
  }

  sendPostRequestAndRenderPartialView(url, requestBody, token, elementToReRender)
})

async function sendPostRequestAndRenderPartialView(
  url: string,
  requestBody: FormData,
  token: string,
  elementToReRender: Element,
) {
  const response = await fetch(url, {
    mode: 'same-origin',
    method: 'POST',
    body: requestBody,
    headers: {
      Accept: 'text/fragment+html',
      'Scoped-CSRF-Token': token,
    },
  })

  const knownServerStatusCodes = [400, 409, 500]

  // Retrieve the partial from the response and re-render it
  if (response.status <= 400 || knownServerStatusCodes.includes(response.status)) {
    const partial = parseHTML(document, await response.text())
    elementToReRender?.parentElement?.replaceChild(partial, elementToReRender)
  }

  // Show an error message (if request fails)
  if (response.status >= 400) {
    displayErrorMessageOnFailure()
  }
}

function displayErrorMessageOnFailure() {
  const errorMessage = document.querySelector('.js-message')?.getAttribute('prebuild-data-error-message')
  const message = document.querySelector<HTMLInputElement>('.js-flash-error')?.value || errorMessage
  if (message) {
    const template = document.querySelector<HTMLTemplateElement>('template.js-flash-template')
    template?.after(new TemplateInstance(template, {className: 'flash-error', message}))
  }
}

// change events aren't dispatched after using #checked, only on keyboard/click interactions
// so we need to manually dispatch this to toggle the submit button
// when we toggle location/locations inputs for the user
function dispatchInputOnChangeEvent(input: HTMLInputElement) {
  input.dispatchEvent(new Event('change', {bubbles: true}))
}

function getUserList() {
  const userList = document.getElementById('actors-to-notify')?.getElementsByTagName('li')
  const userListArray = Array.from(userList ? userList : [])
  const userListUsernames = userListArray.map(li => li.getAttribute('prebuild-data-bulk-actions-id-user'))

  return userListUsernames
}

function getTeamList() {
  const actorList = document.getElementById('actors-to-notify')?.getElementsByTagName('li')
  const actorListArray = Array.from(actorList ? actorList : [])
  const teamListSlugs = actorListArray.map(li => li.getAttribute('prebuild-data-bulk-actions-id-team'))

  return teamListSlugs
}

function createNotifyActorsRequestBody(userList: Array<string | null>, teamList: Array<string | null>) {
  const requestBody = new FormData()
  for (const user of userList) {
    user !== null ? requestBody.append('codespaces_prebuild_configuration[users_to_notify][]', user) : null
  }

  for (const team of teamList) {
    team !== null ? requestBody.append('codespaces_prebuild_configuration[teams_to_notify][]', team) : null
  }

  // ensure request body is not empty
  if (requestBody.get('codespaces_prebuild_configuration[users_to_notify][]') === null) {
    requestBody.append('codespaces_prebuild_configuration[users_to_notify][]', '')
  }

  if (requestBody.get('codespaces_prebuild_configuration[teams_to_notify][]') === null) {
    requestBody.append('codespaces_prebuild_configuration[teams_to_notify][]', '')
  }

  return requestBody
}

function textForDays(details: Element): string | null {
  const dayInputs = Array.from(details.querySelectorAll<HTMLInputElement>('input.js-delivery-day-input'))
  const checkedInputs = dayInputs.filter(input => input.checked)

  if (checkedInputs.length === 0) {
    return null
  }

  if (dayInputs.every(input => input.checked)) {
    return details.getAttribute('data-everyday')!
  }

  const weekdayInputs = dayInputs.filter(input => input.classList.contains('js-delivery-day-weekday'))
  if (weekdayInputs.every(input => input.checked) && checkedInputs.length === weekdayInputs.length) {
    return details.getAttribute('data-weekdays')!
  }

  const weekendInputs = dayInputs.filter(input => input.classList.contains('js-delivery-day-weekend'))
  if (weekendInputs.every(input => input.checked) && checkedInputs.length === weekendInputs.length) {
    return details.getAttribute('data-weekends')!
  }

  if (checkedInputs.length === 2) {
    return `${checkedInputs[0]!.value} and ${checkedInputs[1]!.value}`
  }

  return checkedInputs.map(input => input.value).join(', ')
}

function handleDayChange(details: Element) {
  let title = textForDays(details)
  const detailsSummary = details.querySelector('.js-delivery-day-summary')
  if (detailsSummary == null) {
    return
  }

  if (title == null) {
    title = detailsSummary.getAttribute('data-default-label')!
  }
  const detailsText = detailsSummary.querySelector('.js-delivery-day-summary-text')
  if (detailsText) {
    detailsText.textContent = title
  }
}

function textForTimes(details: Element): string | null {
  const timeInputs = details.querySelectorAll<HTMLInputElement>('input.js-delivery-time-input:checked')

  if (timeInputs.length === 0) {
    return null
  }

  return Array.from(timeInputs)
    .map(input => input.value)
    .join(', ')
}

function checkDayInputValidity(): boolean {
  let fieldValid = true
  const dayInputs = document.querySelectorAll<HTMLInputElement>('.js-delivery-day-input')

  if (dayInputs.length > 0) {
    const allowBlank = dayInputs[0]?.closest('.js-orgs-reminders-day-menu')?.hasAttribute('data-allow-blank')
    fieldValid = allowBlank || Array.from(dayInputs).some(e => e.checked)
    dayInputs[0]!.closest<HTMLElement>('.js-day-form-group')!.classList.toggle('errored', !fieldValid)
  }
  return fieldValid
}

function checkTimeInputValidity(): boolean {
  let fieldValid = true

  // Require at least one time. If `data-allow-blank` is set, only require a time if a day is selected.
  const daysSelected = Array.from(document.querySelectorAll<HTMLInputElement>('.js-delivery-day-input')).some(
    e => e.checked,
  )
  const timeInputs = document.querySelectorAll<HTMLInputElement>('.js-delivery-time-input')
  if (timeInputs.length > 0) {
    const allowBlank = !!timeInputs[0]?.closest('.js-orgs-reminders-time-menu')?.hasAttribute('data-allow-blank')
    fieldValid = Array.from(timeInputs).some(e => e.checked) || (allowBlank && !daysSelected)
  }

  timeInputs[0]!.closest<HTMLElement>('.js-time-form-group')!.classList.toggle('errored', !fieldValid)
  return fieldValid
}

function handleTimeChange(details: Element) {
  const detailsSummary = details.querySelector<HTMLElement>('.js-delivery-time-summary')!
  const title = textForTimes(details) || detailsSummary.getAttribute('data-default-label')!

  const detailsText = detailsSummary.querySelector<HTMLElement>('.js-delivery-time-summary-text')!
  // Update both the text and the title, the title will show all times on hover
  detailsText.textContent = title
  detailsText.setAttribute('title', title)
}

function setDefaultTimeZone(details: Element) {
  const userTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone || ''

  const toCheck = details.querySelector<HTMLInputElement>(
    `.js-delivery-time-zone-input[data-browser-name="${userTimeZone}"], .js-delivery-time-zone-input[data-browser-name="Etc/UTC"]`,
  )!
  changeValue(toCheck, true)
}

// Handle when there is a change to the selected time zone.
//
// This will try to use the browser's time zone as the default if we're creating a new schedule.
function handleTimeZoneChange(details: Element) {
  const checkedInput = details.querySelector('input.js-delivery-time-zone-input:checked')
  if (checkedInput instanceof HTMLInputElement) {
    const title = checkedInput.getAttribute('data-display-name') || ''
    const detailsText = details.querySelector<HTMLSpanElement>('.js-delivery-time-zone-summary-text')!

    detailsText.textContent = title
  } else {
    setDefaultTimeZone(details)
  }
}

on('click', '.js-codespaces-branch', function (event) {
  if (!(event.target instanceof Element)) return
  const details = event.target.closest<HTMLElement>('.js-codespaces-branch')
  const branch = details?.getAttribute('data-refname')
  if (details == null || branch == null) {
    return
  }
  const url = document.getElementById('update-configuration')?.getAttribute('data-controller-path-update-config')
  const token = document.getElementById('update-config-csrf')?.getAttribute('value')

  if (url == null || token == null) {
    return
  }

  const data = new FormData()
  data.append('codespaces_prebuild_configuration[branch]', branch)

  const elementToReRender = document.getElementById('devcontainer-path-select')
  if (elementToReRender == null) {
    return
  }

  sendPostRequestAndRenderPartialView(url, data, token, elementToReRender)
})

on('change', '.js-delivery-day-input', function (event) {
  if (!(event.target instanceof Element)) return
  const details = event.target.closest('.js-orgs-reminders-day-menu')
  if (details == null) {
    return
  }
  handleDayChange(details)
  checkDayInputValidity()
})

on('change', '.js-delivery-time-input', function (event) {
  if (!(event.target instanceof Element)) return
  const details = event.target.closest<HTMLElement>('.js-orgs-reminders-time-menu')!
  handleTimeChange(details)
  checkTimeInputValidity()
})

on('change', '.js-delivery-time-zone-input', function (event) {
  if (!(event.target instanceof Element)) return
  const details = event.target.closest<HTMLElement>('.js-orgs-reminders-time-zone-menu')!
  handleTimeZoneChange(details)
})

// Set values on page load
observe('.js-orgs-reminders-time-menu', handleTimeChange)
observe('.js-orgs-reminders-time-zone-menu', handleTimeZoneChange)
